/**
 * API
 */
import HttpClient from './httpClient';

// API
const API_CONFIG = {
  baseURL: process.env.REACT_APP_API_BASE_URL || 'https://ahut.site:8080/api/smart-clock',
  timeout: 10000,
  retries: 2,
  headers: {
    'Accept': 'application/json',
  }
};

const APIClient = (apiConfig = {}) => {
  // API
  const apiClient = new HttpClient({...API_CONFIG, ...apiConfig});

  //  - token
  apiClient.addRequestInterceptor(
    (requestConfig, fetchOptions, url) => {
      const token = localStorage.getItem('authToken');
      if (token) {
        requestConfig.headers.Authorization = `Bearer ${token}`;
      }
      // ID
      requestConfig.headers['X-Request-ID'] = `req_${Date.now()}_${Math.random().toString(36).substr(2, 9)}`;
      
      return {requestConfig, fetchOptions, url};
    },
    (error) => {
      console.error(' Request Interceptor Error:', error);
      return Promise.reject(error);
    }
  );

  //  - 
  apiClient.addResponseInterceptor(
    async (response) => {
      console.log(' Response:', response.status, response.url);
      
      // 
      if (response.status === 401) {
        // token
        localStorage.removeItem('authToken');
        window.dispatchEvent(new CustomEvent('auth:logout'));
      }
      
      return response;
    },
    (error) => {
      console.error(' Response Interceptor Error:', error);
      
      // 
      if (error.status === 403) {
        window.dispatchEvent(new CustomEvent('auth:forbidden'));
      } else if (error.status === 500) {
        window.dispatchEvent(new CustomEvent('app:serverError'));
      }
      
      return Promise.reject(error);
    }
  );

  //  - 
  apiClient.addErrorInterceptor(
    async (error) => {
      // 
      //  toast
      console.error('[API ERROR]', error);
      return error;
    },
    (error) => {
      console.error(' Response Interceptor Error:', error);
      
      // 
      if (error.status === 403) {
        window.dispatchEvent(new CustomEvent('auth:forbidden'));
      } else if (error.status === 500) {
        window.dispatchEvent(new CustomEvent('app:serverError'));
      }
      
      return Promise.reject(error);
    }
  );

  return apiClient;
}

export default APIClient;