import { I18n } from "i18n-js";

// 
import en from "./en.json";
import zhHans from "./zh-Hans.json";  // 
import zhHant from "./zh-Hant.json";  // 

const i18n = new I18n({
  // 
  en: en,
  
  // 
  "zh-Hans": zhHans,
  
  // 
  "zh-Hant": zhHant,
});

// 
i18n.defaultLocale = "en";
i18n.locale = "zh-Hans";

// 
i18n.enableFallback = true;

// 
i18n.fallbacks = {
  "zh-HK": ["zh-Hant", "zh-TW", "en"],
  "zh": ["zh-Hans", "en"],
  "en": ["en"],
  "en-US": ["en", "en-GB"]
};

// 
i18n.missingBehavior = "message"; // : "message", "guess", 

// 
i18n.onMissingTranslation = (scope, options) => {
  const key = scope.join('.');
  
  // 
  if (process.env.NODE_ENV === 'development') {
    console.warn(` Missing translation: ${key} for locale: ${i18n.locale}`);
    
    // 
    // trackMissingTranslation(key, i18n.locale);
  }
  
  // 
  switch (options?.missingBehavior || 'smart') {
    case 'empty':
      return ''; // 
      
    case 'key':
      return key; // 
      
    case 'last':
      return scope[scope.length - 1]; // 
      
    case 'smart':
    default:
      // 
      const lastKey = scope[scope.length - 1];
      return lastKey
        .replace(/([A-Z])/g, ' $1') // 
        .replace(/[_-]/g, ' ')      // 
        .toLowerCase()
        .replace(/^\w/, c => c.toUpperCase()); // 
  }
};

// 
i18n.interpolation = {
  // 
  defaultVariables: {
    appName: 'iClock',
    version: '1.0.0',
    company: 'Hut Inc.',
    year: new Date().getFullYear()
  },
  
  //  {{}} 
  // prefix: '{{',
  // suffix: '}}',
  
  // 
  missingInterpolationHandler: (text, value, options) => {
    if (process.env.NODE_ENV === 'development') {
      console.warn(` Missing interpolation variable: ${value} in text: "${text}"`);
    }
    
    // 
    return `[${value}]`;
  }
};

export default i18n;