/**
 * 
 * 
 */

class Logger {
  constructor() {
    // 
    this.isDev = process.env.NODE_ENV === 'development';
  }

  /**
   * 
   */
  log(...args) {
    if (this.isDev) {
      console.log('[DEV]', ...args);
    }
  }

  /**
   * 
   */
  info(...args) {
    if (this.isDev) {
      console.info('[DEV-INFO]', ...args);
    }
  }

  /**
   * 
   */
  warn(...args) {
    if (this.isDev) {
      console.warn('[DEV-WARN]', ...args);
    }
  }

  /**
   * 
   */
  error(...args) {
    if (this.isDev) {
      console.error('[DEV-ERROR]', ...args);
    }
  }

  /**
   * 
   */
  debug(...args) {
    if (this.isDev) {
      console.debug('[DEV-DEBUG]', ...args);
    }
  }

  /**
   * 
   */
  lifecycle(...args) {
    if (this.isDev) {
      console.log('[]', ...args);
    }
  }

  /**
   * 
   */
  cache(...args) {
    if (this.isDev) {
      console.log('[]', ...args);
    }
  }

  /**
   * API
   */
  api(...args) {
    if (this.isDev) {
      console.log('[API]', ...args);
    }
  }

  /**
   * 
   */
  performance(label, fn) {
    if (this.isDev) {
      console.time(`[] ${label}`);
      const result = fn();
      console.timeEnd(`[] ${label}`);
      return result;
    }
    return fn();
  }

  /**
   * 
   */
  async performanceAsync(label, asyncFn) {
    if (this.isDev) {
      console.time(`[] ${label}`);
      const result = await asyncFn();
      console.timeEnd(`[] ${label}`);
      return result;
    }
    return await asyncFn();
  }

  /**
   * 
   */
  group(label, fn) {
    if (this.isDev) {
      console.group(`[DEV] ${label}`);
      fn();
      console.groupEnd();
    }
  }

  /**
   * 
   */
  table(data, label = '') {
    if (this.isDev) {
      if (label) console.log(`[DEV-TABLE] ${label}`);
      console.table(data);
    }
  }
}

// 
const logger = new Logger();

// 
export default logger;
export { Logger };

