/**
 * PersistentStorage - 
 *  localStorage 
 */

import logger from '../logger';

class PersistentStorage {
  constructor(namespace = 'app') {
    this.namespace = namespace;
    this.defaultTTL = 5 * 60 * 1000; // 5
  }

  /**
   * 
   * @param {string} key - 
   * @returns {string} 
   */
  _getCacheKey(key) {
    return `${this.namespace}_${key}`;
  }

  /**
   * 
   * @param {string} key - 
   * @param {any} data - 
   * @param {number} ttl - 5
   * @returns {boolean} 
   */
  set(key, data, ttl = this.defaultTTL) {
    try {
      const cacheData = {
        data: data,
        timestamp: Date.now(),
        expiration: ttl,
        expiresAt: Date.now() + ttl
      };
      
      const cacheKey = this._getCacheKey(key);
      localStorage.setItem(cacheKey, JSON.stringify(cacheData));
      
      logger.cache(`: ${key}`, {
        ttl: `${ttl / 1000}`,
        expiresAt: new Date(cacheData.expiresAt).toLocaleString()
      });
      
      return true;
    } catch (error) {
      logger.error(`[PersistentStorage]  (${key}):`, error);
      return false;
    }
  }

  /**
   * 
   * @param {string} key - 
   * @param {boolean} ignoreTTL - 
   * @returns {any|null}  null
   */
  get(key, ignoreTTL = false) {
    try {
      const cacheKey = this._getCacheKey(key);
      const cachedItem = localStorage.getItem(cacheKey);
      
      if (!cachedItem) {
        logger.cache(`: ${key}`);
        return null;
      }
      
      const cacheData = JSON.parse(cachedItem);
      const now = Date.now();
      
      // 
      if (!ignoreTTL && now > cacheData.expiresAt) {
        localStorage.removeItem(cacheKey);
        logger.cache(`: ${key}`);
        return null;
      }
      
      logger.cache(`: ${key}`, {
        age: `${Math.round((now - cacheData.timestamp) / 1000)}`,
        remainingTTL: ignoreTTL ? '' : `${Math.round((cacheData.expiresAt - now) / 1000)}`
      });
      
      return cacheData.data;
    } catch (error) {
      logger.error(`[PersistentStorage]  (${key}):`, error);
      return null;
    }
  }

  /**
   * 
   * @param {string} key - 
   * @returns {boolean} 
   */
  has(key) {
    return this.get(key) !== null;
  }

  /**
   * 
   * @param {string} key - 
   * @returns {boolean} 
   */
  delete(key) {
    try {
      const cacheKey = this._getCacheKey(key);
      localStorage.removeItem(cacheKey);
      logger.log(`[PersistentStorage] : ${key}`);
      return true;
    } catch (error) {
      logger.error(`[PersistentStorage]  (${key}):`, error);
      return false;
    }
  }

  /**
   * 
   * @returns {number} 
   */
  clear() {
    try {
      const keys = Object.keys(localStorage);
      const namespacePrefix = `${this.namespace}_`;
      let count = 0;
      
      keys.forEach(key => {
        if (key.startsWith(namespacePrefix)) {
          localStorage.removeItem(key);
          count++;
        }
      });
      
      logger.log(`[PersistentStorage]  ${count} `);
      return count;
    } catch (error) {
      logger.error('[PersistentStorage] :', error);
      return 0;
    }
  }

  /**
   * 
   * @returns {number} 
   */
  clearExpired() {
    try {
      const keys = Object.keys(localStorage);
      const namespacePrefix = `${this.namespace}_`;
      const now = Date.now();
      let count = 0;
      
      keys.forEach(key => {
        if (key.startsWith(namespacePrefix)) {
          try {
            const cachedItem = localStorage.getItem(key);
            if (cachedItem) {
              const cacheData = JSON.parse(cachedItem);
              if (now > cacheData.expiresAt) {
                localStorage.removeItem(key);
                count++;
              }
            }
          } catch (error) {
            // 
            localStorage.removeItem(key);
            count++;
          }
        }
      });
      
      logger.log(`[PersistentStorage]  ${count} `);
      return count;
    } catch (error) {
      logger.error('[PersistentStorage] :', error);
      return 0;
    }
  }

  /**
   * 
   * @returns {Array} 
   */
  getInfo() {
    try {
      const keys = Object.keys(localStorage);
      const namespacePrefix = `${this.namespace}_`;
      const now = Date.now();
      const cacheInfo = [];
      
      keys.forEach(key => {
        if (key.startsWith(namespacePrefix)) {
          try {
            const cachedItem = localStorage.getItem(key);
            if (cachedItem) {
              const cacheData = JSON.parse(cachedItem);
              const originalKey = key.replace(namespacePrefix, '');
              
              cacheInfo.push({
                key: originalKey,
                size: new Blob([cachedItem]).size,
                timestamp: cacheData.timestamp,
                expiresAt: cacheData.expiresAt,
                isExpired: now > cacheData.expiresAt,
                age: now - cacheData.timestamp,
                remainingTTL: Math.max(0, cacheData.expiresAt - now)
              });
            }
          } catch (error) {
            // 
          }
        }
      });
      
      return cacheInfo.sort((a, b) => b.timestamp - a.timestamp);
    } catch (error) {
      logger.error('[PersistentStorage] :', error);
      return [];
    }
  }

  /**
   * 
   * @param {string} key - 
   * @param {Function} fetchFn - 
   * @param {number} ttl - 
   * @returns {Promise<any>} 
   */
  async getOrSet(key, fetchFn, ttl = this.defaultTTL) {
    // 
    const cachedData = this.get(key);
    if (cachedData !== null) {
      return cachedData;
    }
    
    // 
    try {
      logger.log(`[PersistentStorage] : ${key}`);
      const data = await fetchFn();
      this.set(key, data, ttl);
      return data;
    } catch (error) {
      logger.error(`[PersistentStorage]  (${key}):`, error);
      throw error;
    }
  }
}

