// 3.  React 
import React, { useEffect, useState } from 'react';
import cache from '@/utils/storage/PersistentStorage';
import { smartClockService } from '@/services';

const WeatherComponent = () => {
  const [weatherData, setWeatherData] = useState(null);
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    const fetchWeatherData = async () => {
      try {
        //  getOrSet 
        const data = await cache.getOrSet(
          'weather_data',
          () => smartClockService.getCurrentTempInfo(),
          5 * 60 * 1000 // 5
        );
        
        setWeatherData(data);
      } catch (error) {
        logger.error(':', error);
      } finally {
        setLoading(false);
      }
    };

    fetchWeatherData();
  }, []);

  // 
  const handleRefresh = async () => {
    setLoading(true);
    cache.delete('weather_data'); // 
    
    try {
      const data = await smartClockService.getCurrentTempInfo();
      cache.set('weather_data', data, 5 * 60 * 1000);
      setWeatherData(data);
    } catch (error) {
      logger.error(':', error);
    } finally {
      setLoading(false);
    }
  };

  return (
    <div>
      <button onClick={handleRefresh}></button>
      {loading ? <div>...</div> : <div>{JSON.stringify(weatherData)}</div>}
    </div>
  );
};

