export const createDataTable = (response) => {
    const data_history = {
      labels: response.labels,
      datasets: [
        {
          label: "(C)",
          data: response.temp,
          fill: false,
          backgroundColor: "rgba(75,192,192,0.2)",
          borderColor: "#FF8585",
          borderCapStyle: 'round',
          pointStyle: false,
          yAxisID: 'y1'
        },
        {
          label: "()",
          data: response.humi,
          fill: false,
          borderColor: "#99DCDC",
          borderCapStyle: 'round',
          pointStyle: false,
          yAxisID: 'y2'
        }
      ]
    };
    return data_history;
}

