export const createDataTableForAnalyse = (response) => {
    let normalVale = 0
    const outTemp = response.temp.map( (temp, index) => {
      const outTemp = response.outdoors_temp[index];
      let dTemp = outTemp;
      dTemp = dTemp.toFixed(2);
      dTemp = Math.abs(dTemp)
      // Here is handle the unusual data
      if (outTemp == 0) {
        dTemp = normalVale
      } else {
        normalVale = dTemp
      }
      return dTemp;
    })

    normalVale = 0
    const diffTemp = response.temp.map( (temp, index) => {
      const outTemp = response.outdoors_temp[index];
      let dTemp = outTemp-temp;
      dTemp = dTemp.toFixed(2);
      // Here is handle the unusual data
      if (Math.abs(dTemp) > 10.0 && outTemp == 0) {
        dTemp = normalVale
      } else {
        normalVale = dTemp
      }
      return dTemp;
    })

    const data_history = {
      labels: response.labels,
      datasets: [
        {
          label: "(C)",
          data: response.temp,
          fill: false,
          backgroundColor: "rgba(75,192,192,0.2)",
          borderColor: "#FB6B6B",
          borderCapStyle: 'round',
          pointStyle: false,
          yAxisID: 'y1'
        },
        {
          label: "(C)",
          data: outTemp,
          fill: false,
          borderColor: "#1AB8FD",
          borderCapStyle: 'round',
          pointStyle: false,
          yAxisID: 'y1'
        },
        {
          label: "(C)",
          data: diffTemp,
          fill: false,
          borderColor: "#676767",
          borderCapStyle: 'round',
          pointStyle: false,
          yAxisID: 'y2'
        }
      ]
    };
    return data_history;
}